<template>
  <div class="row">
    <div class="col-md-12">
      <alert 
        :alert="alert"
      />
      <div class="col-md-6 col-sm-6">
        <router-link 
          v-if="highLights.length !== 0"
          class="btn btn-primary btn-link" 
          to="/highLights/add"
        >
          Add new HighLight
        </router-link>
      </div>
      <SearchBox
        @handleSearch="search"
        @handleSelect="updateSearchType"
        :selectOptions="types"
        placeholder="Find Item"
        selected="events"
        selectable
      />
      <Selectable
        @onSearch="searchParentItem"
        @onChange="search"
        :options="setFoundedItems"
        generalKey="parentId"
      />
      <div class="clearfix"></div>
    </div>
    <div class="col-md-12">
      <div class="card" v-if="highLights.length !== 0">
        <paper-table 
          :title="table.title" 
          :sub-title="table.subTitle" 
          :data="highLights" 
          :columns="table.columns"
          :actions="table.actions"
          :handleOrder="orderHightlights"
          @clickDelete="removeHighLight"
        />
        <button
          class="btn btn-primary pull-right newUserButton"
          @click="showMoreHighLights"
          v-if="isMore || count > 10"
        >
          Show more
        </button>
      </div>
      <jumbotron 
        title="Ups nothing"
        text="Please add more Highlights"
        v-if="highLights.length === 0"
      >
        <router-link 
          class="btn btn-primary" 
          to="/highlights/add"
        >
          Add new highlight
        </router-link>
      </jumbotron>
    </div>
  </div>
</template> 

<script>
  import { mapGetters, mapActions, mapState, mapMutations } from 'vuex'
  import { tableColumns, tableActions, types } from './TableMock.js'

  export default {
    components: {
      PaperTable: () => import('ui/PaperTable.vue'),
      Selectable: () => import('ui/Selectable/Selectable.vue')
    },
    data: () => ({
      tableActions,
      tableColumns,
      types,
      table: {
        title: 'Highlight list',
        subTitle: 'Highlight existing list',
        columns: [...tableColumns],
        actions: [...tableActions],
        data: []
      }
    }),
    mounted() {
      this.fetchHighLights()
    },
    watch: {
      highLights(value) {
        if (value) {
          this.table.data = [
            ...value
          ]
        }
      }
    },
    computed: {
      ...mapGetters({
        highLights: 'getHighLights',
        setFoundedItems: 'setFoundedItems'
      }),
      ...mapState({
        alert: state => state.alert,
        user: state => state.user,
        count: ({ highLights: { pagination } }) => pagination.count,
        isMore: ({ highLights: { isMoreHighLightsAvialiable } }) => isMoreHighLightsAvialiable
      })
    },
    methods: {
      search(searchQuery) {
        const query = searchQuery.parentId
          ? { parent_id: searchQuery.parentId }
          : { s: searchQuery }
        this.globalSearchHighLight(query)
      },
      ...mapMutations([
        'updateSearchType'
      ]),
      ...mapActions([
        'fetchHighLights',
        'removeHighLight',
        'showMoreHighLights',
        'globalSearchHighLight',
        'searchParentItem',
        'orderHightlights'
      ])
    }
  }
</script>

<style lang="css" scoped>
  .newUserButton {
    margin: 10px 0px;
    display: block;
    width: 100%;
  }

  .alert {
    margin: 0px 0px 10px;
  }

  .btn-link {
    margin-bottom: 15px;
  }
</style> 



